import { useTranslation } from "react-i18next";
import React from "react";
import Select from "react-select";
import {
  FaCode,
  FaColumns,
  FaExchangeAlt,
  FaFileAlt,
  FaPercent,
  FaPlus,
  FaSync,
  FaTextWidth,
  FaTrash,
} from "react-icons/fa";
import {
  Button,
  Form,
  FormGroup,
  FormText,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
  Modal,
  ModalHeader,
  ModalBody,
} from "reactstrap";
import { Formik } from "formik";
import * as Yup from "yup";

import "./UpdateModal.css";

import AsteriskIcon from "components/common/AsteriskIcon/AsteriskIcon";
import i18n from "i18n/i18n";

export default function UpdateModal({
  selectedItem,
  modal,
  toggle,
  headerTitle,
  handleUpdateOnClick,
  handleCancelOnClick,
  evaluationTypes,
  subjectOutputStandards,
}) {
  const { t } = useTranslation();

  const subjectOutputStandardsOption = subjectOutputStandards.map((item) => {
    return { value: item.id, label: item.ma };
  });

  return (
    <Modal isOpen={modal} toggle={toggle}>
      <ModalHeader toggle={toggle}>{headerTitle}</ModalHeader>
      <ModalBody>
        <div className="page">
          <div className="page-content">
            <Formik
              initialValues={{
                id: selectedItem ? selectedItem.id : "",
                ma: selectedItem ? selectedItem.ma : "",
                ten: selectedItem ? selectedItem.ten : "",
                moTa: selectedItem ? selectedItem.moTa : "",
                tyLe: selectedItem ? selectedItem.tyLe : "",
                chuanDauRaMonHoc: selectedItem
                  ? selectedItem.chuanDauRaMonHoc
                  : [],
                loaiDanhGia: selectedItem
                  ? selectedItem.loaiDanhGia
                  : evaluationTypes[0]
                  ? evaluationTypes[0].id
                  : "",
              }}
              enableReinitialize={true}
              validationSchema={Yup.object().shape({
                ma: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
                ten: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
                moTa: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
                tyLe: Yup.number().required(i18n.t("REQUIRED_FIELD_WARNING")),
              })}
            >
              {({
                values,
                errors,
                isValid,
                handleChange,
                handleReset,
                setFieldValue,
              }) => (
                <Form>
                  <FormGroup className="row">
                    <Label for="ma" className="col-lg-3">
                      {t("admin.evaluation_activity_edit.CODE")}{" "}
                      <AsteriskIcon />
                    </Label>
                    <div className="col-lg-9">
                      <InputGroup>
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <FaCode />
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input
                          type="text"
                          name="ma"
                          placeholder={t(
                            "admin.evaluation_activity_edit.CODE_PLACEHOLDER"
                          )}
                          value={values.ma}
                          onChange={handleChange}
                          invalid={errors.ma}
                        />
                      </InputGroup>
                      {errors.ma && (
                        <FormText color="danger">{errors.ma}</FormText>
                      )}
                    </div>
                  </FormGroup>{" "}
                  <br />
                  <FormGroup className="row">
                    <Label for="ten" className="col-lg-3">
                      {t("admin.evaluation_activity_edit.NAME")}{" "}
                      <AsteriskIcon />
                    </Label>
                    <div className="col-lg-9">
                      <InputGroup>
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <FaTextWidth />
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input
                          type="text"
                          name="ten"
                          placeholder={t(
                            "admin.evaluation_activity_edit.NAME_PLACEHOLDER"
                          )}
                          value={values.ten}
                          onChange={handleChange}
                          invalid={errors.ten}
                        />
                      </InputGroup>
                      {errors.ten && (
                        <FormText color="danger">{errors.ten}</FormText>
                      )}
                    </div>
                  </FormGroup>{" "}
                  <br />
                  <FormGroup className="row">
                    <Label for="moTa" className="col-lg-3">
                      {t("admin.evaluation_activity_edit.DESCRIPTION")}{" "}
                      <AsteriskIcon />
                    </Label>
                    <div className="col-lg-9">
                      <InputGroup>
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <FaFileAlt />
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input
                          type="textarea"
                          name="moTa"
                          placeholder={t(
                            "admin.evaluation_activity_edit.DESCRIPTION_PLACEHOLDER"
                          )}
                          value={values.moTa}
                          onChange={handleChange}
                          invalid={errors.moTa}
                        />
                      </InputGroup>
                      {errors.moTa && (
                        <FormText color="danger">{errors.moTa}</FormText>
                      )}
                    </div>
                  </FormGroup>{" "}
                  <br />
                  <FormGroup className="row">
                    <Label for="chuanDauRaMonHoc" className="col-lg-3">
                      {t(
                        "admin.evaluation_activity_edit.SUBJECT_OUTPUT_STANDARD"
                      )}
                    </Label>
                    <div className="col-lg-9">
                      <Select
                        value={values.chuanDauRaMonHoc}
                        onChange={(e) =>
                          setFieldValue(
                            "chuanDauRaMonHoc",
                            Array.isArray(e) ? e : []
                          )
                        }
                        isMulti
                        closeMenuOnSelect={false}
                        options={subjectOutputStandardsOption}
                        placeholder={t(
                          "admin.evaluation_activity_edit.SUBJECT_OUTPUT_STANDARD_PLACEHOLDER"
                        )}
                      />
                    </div>
                  </FormGroup>{" "}
                  <br />
                  <FormGroup className="row">
                    <Label for="tyLe" className="col-lg-3">
                      {t("admin.evaluation_activity_edit.PERCENT")}{" "}
                      <AsteriskIcon />
                    </Label>
                    <div className="col-lg-6">
                      <InputGroup>
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <FaPercent />
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input
                          type="number"
                          name="tyLe"
                          placeholder={t(
                            "admin.evaluation_activity_edit.PERCENT_PLACEHOLDER"
                          )}
                          value={values.tyLe}
                          onChange={handleChange}
                          invalid={errors.tyLe}
                        />
                      </InputGroup>
                      {errors.tyLe && (
                        <FormText color="danger">{errors.tyLe}</FormText>
                      )}
                    </div>
                  </FormGroup>{" "}
                  <br />
                  <FormGroup className="row">
                    <Label for="loaiDanhGia" className="col-lg-3">
                      {t("admin.evaluation_activity_edit.EVALUATION_TYPE")}
                    </Label>
                    <div className="col-lg-9">
                      <InputGroup>
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <FaColumns />
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input
                          type="select"
                          name="loaiDanhGia"
                          value={values.loaiDanhGia}
                          onChange={handleChange}
                        >
                          {evaluationTypes &&
                            evaluationTypes.map((item, index) => {
                              return (
                                <option key={index} value={item.id}>
                                  {item.ma}
                                </option>
                              );
                            })}
                        </Input>
                      </InputGroup>
                    </div>
                  </FormGroup>{" "}
                  <br />
                  <FormGroup>
                    <Button
                      color="success"
                      disabled={!isValid}
                      onClick={() => handleUpdateOnClick(values, isValid)}
                    >
                      <FaExchangeAlt /> {t("admin.common.UPDATE")}
                    </Button>
                    &emsp;
                    <Button onClick={handleReset}>
                      <FaSync /> {t("admin.common.REFRESH")}
                    </Button>
                    &emsp;
                    <Button color="danger" onClick={handleCancelOnClick}>
                      <FaTrash /> {t("admin.common.CANCEL")}
                    </Button>
                  </FormGroup>
                </Form>
              )}
            </Formik>
          </div>
        </div>
      </ModalBody>
    </Modal>
  );
}
