import React from "react";
import { useTranslation } from "react-i18next";
import { Col, Row } from "reactstrap";

import "./TableFooter.css";

import MyPagination from "./components/MyPagination/MyPagination";

export default function TableFooter({
  currentStartPage,
  currentPage,
  currentLimit,
  currentTotal,
  firstOnClick,
  previousOnClick,
  oneOnClick,
  twoOnClick,
  threeOnClick,
  nextOnClick,
  lastOnClick,
}) {
  const { t } = useTranslation();

  return (
    <Row className="table-footer">
      <Col lg={6}>
        {t("admin.common.DISPLAY")}&nbsp;
        <b>{(currentPage - 1) * currentLimit + (currentTotal > 0 ? 1 : 0)}</b>&nbsp; -&nbsp;
        <b>
          {currentTotal < currentPage * currentLimit
            ? currentTotal
            : currentPage * currentLimit}
        </b>
        &nbsp;
        {t("admin.common.ON")} &nbsp;
        <b>{currentTotal}</b>&nbsp;
        {t("admin.common.ITEMS")}
      </Col>
      <Col lg={6} className="table-pagination">
        
      </Col>
    </Row>
  );
}
