import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { FaCalendar, FaColumns, FaTextWidth } from "react-icons/fa";
import { FormGroup } from "reactstrap";
import { toast } from "react-toastify";

import "./PracticeTeachingPlan.css";

import teachingPlanTypeAPI from "api/common/teaching-plan-type";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import ConfirmModal from "components/admin/ConfirmModal/ConfirmModal";
import TableHeader from "./components/TableHeader/TableHeader";
import TableTools from "./components/TableTools/TableTools";
import TableFooter from "./components/TableFooter/TableFooter";
import TableBody from "./components/TableBody/TableBody";
import AddModal from "./components/AddModal/AddModal";
import UpdateModal from "./components/UpdateModal/UpdateModal";

export default function PracticeTeachingPlan({
  syllabus,
  values,
  errors,
  setFieldValue,
}) {
  const { t } = useTranslation();

  const [isLoading, setIsLoading] = useState(false);
  const [pageable, setPageable] = useState({
    startPage: 1,
    page: 1,
    limit: 8,
  });
  const titles = [
    {
      field: "tuan",
      text: t("admin.topic_list.WEEK"),
      icon: <FaCalendar />,
    },
    {
      field: "ten",
      text: t("admin.topic_list.NAME"),
      icon: <FaTextWidth />,
    },
    {
      field: "chuanDauRaMonHoc",
      text: t("admin.topic_list.SUBJECT_OUTPUT_STANDARD"),
      icon: <FaColumns />,
    },
    {
      field: "hoatDongDayHoc",
      text: t("admin.topic_list.TEACHING_ACTIVITY"),
      icon: <FaColumns />,
    },
    {
      field: "hoatDongDanhGia",
      text: t("admin.topic_list.EVALUATION_ACTIVITY"),
      icon: <FaColumns />,
    },
  ];
  const [items, setItems] = useState([]);
  const [total, setTotal] = useState(0);
  const [deleteBtnIsHidden, setDeleteBtnIsHidden] = useState(true);
  const [confirmModal, setConfirmModal] = useState(false);
  const confirmToggle = function () {
    setConfirmModal(!confirmModal);
  };
  const [teachingPlanType, setTeachingPlanType] = useState(null);
  const [addModal, setAddModal] = useState(false);
  const addToggle = function () {
    setAddModal(!addModal);
  };
  const [selectedItem, setSelectedItem] = useState(null);
  const [updateModal, setUpdateModal] = useState(false);
  const updateToggle = function () {
    setUpdateModal(!updateModal);
  };

  useEffect(async () => {
    try {
      const PRACTICE_ID = 1;
      const teachingPlanTypesRes = await teachingPlanTypeAPI.getAll();
      for (let i = 0; i < teachingPlanTypesRes.contents.length; i++) {
        if (teachingPlanTypesRes.contents[i].id === PRACTICE_ID) {
          setTeachingPlanType(teachingPlanTypesRes.contents[i]);
          break;
        }
      }// eslint-disable-next-line
    } catch (err) {
    }
  }, []);

  const getList = function (pageable, list) {
    let startIndex = 0;
    let endIndex = pageable.limit;
    if (pageable.page > 1) {
      startIndex = (pageable.page - 1) * pageable.limit;
      endIndex = pageable.page * pageable.limit;
    }
    return list.slice(startIndex, endIndex);
  };

  const deleteByIds = function (ids) {
    ids.sort((a, b) => a < b);
    const newList = [...values.practiceTeachingPlan_topics];
    let count = 0;
    for (let id of ids) {
      id = id - count;
      newList.splice(id, 1);
      for (let i = id; i < newList.length; i++) {
        newList[i] = { ...newList[i], id: i };
      }
      count++;
    }
    setFieldValue("practiceTeachingPlan_topics", newList);
    updateTable(pageable, newList);
  };

  const updateTable = function (pageable, list) {
    setIsLoading(true);
    try {
      setItems(getList(pageable, list));
      setTotal(list.length);
    } catch (err) {
      if (err.response) {
        if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
          toast.error(t("RESPONSE_MESSAGE403"));
        }
        else if (err?.response?.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("RESPONSE_MESSAGE404"));
        } else {
          toast.error(t("RESPONSE_MESSAGE500"));
        }
      }
    } finally {
      setIsLoading(false);
    }
  };

  const removeCheckAction = function () {
    const rows = document.getElementsByClassName(
      "practice-teaching-plan-row-table"
    );
    const checkboxs = document.getElementsByName(
      "practice-teaching-plan-row-checkbox"
    );
    setDeleteBtnIsHidden(true);
    for (let i = 0; i < rows.length; i++) {
      rows[i].classList.remove("selected-row");
      checkboxs[i].checked = false;
    }
    const checkAllBox = document.getElementById(
      "practice-teaching-plan-check-all"
    );
    checkAllBox.checked = false;
  };

  const handleDeleteOnClick = function (e) {
    e.preventDefault();
    confirmToggle();
  };

  const handleOkButtonFromConfirmModalOnClick = function (e) {
    e.preventDefault();
    const checkboxs = document.getElementsByName(
      "practice-teaching-plan-row-checkbox"
    );
    const ids = [];
    for (let i = 0; i < checkboxs.length; i++) {
      if (checkboxs[i].checked) {
        ids.push(checkboxs[i].value);
      }
    }
    deleteByIds(ids);
    confirmToggle();
    removeCheckAction();
    toast.success("DELETE_SUCCESS");
  };

  const handleCancelButtonFromConfirmModalOnClick = function (e) {
    e.preventDefault();
    confirmToggle();
  };

  const handleRefreshOnClick = function (e) {
    e.preventDefault();
    removeCheckAction();
    const newPageable = {
      startPage: 1,
      page: 1,
      limit: 8,
    };
    setPageable(newPageable);
    updateTable(newPageable, values.practiceTeachingPlan_topics);
  };

  const handleAddOnClick = function (e) {
    e.preventDefault();
    addToggle();
  };

  const handleLimitOnChange = function (e) {
    removeCheckAction();
    const newPageable = {
      ...pageable,
      startPage: 1,
      page: 1,
      limit: e.target.value,
    };
    setPageable(newPageable);
    updateTable(newPageable, values.practiceTeachingPlan_topics);
  };

  const handleCheckItemOnChange = function (id) {
    const row = document.getElementById("practice-teaching-plan-row-" + id);
    const checkbox = document.getElementById(
      "practice-teaching-plan-checkbox-" + id
    );
    if (checkbox.checked) {
      setDeleteBtnIsHidden(false);
      row.classList.add("selected-row");
    } else {
      const checkAll = document.getElementById(
        "practice-teaching-plan-check-all"
      );
      checkAll.checked = false;
      row.classList.remove("selected-row");

      let isChecked = false;
      const checkboxs = document.getElementsByName(
        "practice-teaching-plan-row-checkbox"
      );
      for (let i = 0; i < checkboxs.length; i++) {
        if (checkboxs[i].checked) {
          isChecked = true;
          break;
        }
      }
      if (!isChecked) {
        setDeleteBtnIsHidden(true);
      }
    }
  };

  const handleCheckAllOnChange = function (e) {
    const rows = document.getElementsByClassName(
      "practice-teaching-plan-row-table"
    );
    const checkboxs = document.getElementsByName(
      "practice-teaching-plan-row-checkbox"
    );
    if (e.target.checked) {
      setDeleteBtnIsHidden(false);
      for (let i = 0; i < rows.length; i++) {
        rows[i].classList.add("selected-row");
        checkboxs[i].checked = true;
      }
    } else {
      setDeleteBtnIsHidden(true);
      for (let i = 0; i < rows.length; i++) {
        rows[i].classList.remove("selected-row");
        checkboxs[i].checked = false;
      }
    }
  };

  const handleUpdateOnClick = function (id) {
    setSelectedItem(values.practiceTeachingPlan_topics[id]);
    updateToggle();
  };

  const handleAddButtonFromAddModalOnClick = function (newItem, isValid) {
    if (!isValid) {
      return;
    }
    const newList = [
      ...values.practiceTeachingPlan_topics,
      {
        ...newItem,
        id: values.practiceTeachingPlan_topics.length,
      },
    ];
    setFieldValue("practiceTeachingPlan_topics", newList);
    updateTable(pageable, newList);
    addToggle();
  };

  const handleCancelButtonFromAddModalOnClick = function (e) {
    e.preventDefault();
    addToggle();
  };

  const handleUpdateButtonFromUpdateModalOnClick = function (newItem, isValid) {
    if (!isValid) {
      return;
    }
    const newList = [...values.practiceTeachingPlan_topics];
    newList[newItem.id] = { ...newItem };
    setFieldValue("practiceTeachingPlan_topics", newList);
    updateTable(pageable, newList);
    updateToggle();
  };

  const handleCancelButtonFromUpdateModalOnClick = function (e) {
    e.preventDefault();
    updateToggle();
  };

  const firstOnClick = function (e) {
    e.preventDefault();
    const currentPageable = {
      ...pageable,
      startPage: 1,
      page: 1,
    };
    setPageable(currentPageable);
    updateTable(currentPageable, values.practiceTeachingPlan_topics);
    removeCheckAction();
  };

  const previousOnClick = function (e) {
    e.preventDefault();
    const { startPage, page } = pageable;
    const currentPageable = {
      ...pageable,
      startPage: page <= startPage ? startPage - 1 : startPage,
      page: page - 1,
    };
    setPageable(currentPageable);
    updateTable(currentPageable, values.practiceTeachingPlan_topics);
    removeCheckAction();
  };

  const oneOnClick = function (e) {
    e.preventDefault();
    const { startPage } = pageable;
    const currentPageable = {
      ...pageable,
      startPage: startPage,
      page: startPage,
    };
    setPageable(currentPageable);
    updateTable(currentPageable, values.practiceTeachingPlan_topics);
    removeCheckAction();
  };

  const twoOnClick = function (e) {
    e.preventDefault();
    const { startPage } = pageable;
    const currentPageable = {
      ...pageable,
      startPage: startPage,
      page: startPage + 1,
    };
    setPageable(currentPageable);
    updateTable(currentPageable, values.practiceTeachingPlan_topics);
    removeCheckAction();
  };

  const threeOnClick = function (e) {
    e.preventDefault();
    const { startPage } = pageable;
    const currentPageable = {
      ...pageable,
      startPage: startPage,
      page: startPage + 2,
    };
    setPageable(currentPageable);
    updateTable(currentPageable, values.practiceTeachingPlan_topics);
    removeCheckAction();
  };

  const nextOnClick = function (e) {
    e.preventDefault();
    const { startPage, page } = pageable;
    const currentPageable = {
      ...pageable,
      startPage: page >= startPage + 2 ? startPage + 1 : startPage,
      page: page + 1,
    };
    setPageable(currentPageable);
    updateTable(currentPageable, values.practiceTeachingPlan_topics);
    removeCheckAction();
  };

  const lastOnClick = function (e) {
    e.preventDefault();
    const { limit, total } = pageable;
    const currentPageable = {
      ...pageable,
      startPage: parseInt(total / limit) + (total % limit === 0 ? 0 : 1),
      page: parseInt(total / limit) + (total % limit === 0 ? 0 : 1),
    };
    setPageable(currentPageable);
    updateTable(currentPageable, values.practiceTeachingPlan_topics);
    removeCheckAction();
  };

  return (
    <>
      <FormGroup className="row">
        <div className="col-lg-12">
          <div className="page">
            {isLoading && <LoadingBar />}

            <ConfirmModal
              modal={confirmModal}
              toggle={confirmToggle}
              headerTitle={t("admin.common.DELETE_CONFIRM_HEADER")}
              bodyContent={t("admin.common.DELETE_CONFIRM_BODY")}
              handleOkButtonOnClick={handleOkButtonFromConfirmModalOnClick}
              handleCancelButtonOnClick={
                handleCancelButtonFromConfirmModalOnClick
              }
            />

            <AddModal
              modal={addModal}
              toggle={addToggle}
              headerTitle={t("admin.common.ADD_HEADER")}
              handleAddOnClick={handleAddButtonFromAddModalOnClick}
              handleCancelOnClick={handleCancelButtonFromAddModalOnClick}
              teachingPlanTypes={[teachingPlanType]}
              subjectOutputStandards={
                values.outputStandard_subjectOutputStandards
              }
              teachingActivities={values.teachingActivity_teachingActivities}
              evaluationActivities={values.evaluation_evaluationActivities}
            />

            <UpdateModal
              selectedItem={selectedItem}
              modal={updateModal}
              toggle={updateToggle}
              headerTitle={t("admin.common.UPDATE_HEADER")}
              handleUpdateOnClick={handleUpdateButtonFromUpdateModalOnClick}
              handleCancelOnClick={handleCancelButtonFromUpdateModalOnClick}
              teachingPlanTypes={[teachingPlanType]}
              subjectOutputStandards={
                values.outputStandard_subjectOutputStandards
              }
              teachingActivities={values.teachingActivity_teachingActivities}
              evaluationActivities={values.evaluation_evaluationActivities}
            />

            <TableHeader
              headerTitle={t("admin.topic_list.TOPIC_TABLE")}
              deleteBtnIsHidden={deleteBtnIsHidden}
              handleDeleteOnClick={handleDeleteOnClick}
              handleRefreshOnClick={handleRefreshOnClick}
              handleAddOnClick={handleAddOnClick}
            />

            <TableTools
              currentLimit={pageable.limit}
              handleLimitOnChange={handleLimitOnChange}
            />

            <TableBody
              titles={titles}
              items={values.practiceTeachingPlan_topics}
              handleCheckItemOnChange={handleCheckItemOnChange}
              handleCheckAllOnChange={handleCheckAllOnChange}
              handleEditOnClick={handleUpdateOnClick}
            />

            <TableFooter
              currentStartPage={pageable.startPage}
              currentPage={pageable.page}
              currentLimit={pageable.limit}
              currentTotal={values.practiceTeachingPlan_topics.length}
              firstOnClick={firstOnClick}
              previousOnClick={previousOnClick}
              oneOnClick={oneOnClick}
              twoOnClick={twoOnClick}
              threeOnClick={threeOnClick}
              nextOnClick={nextOnClick}
              lastOnClick={lastOnClick}
            />
          </div>
        </div>
      </FormGroup>{" "}
      <br />
    </>
  );
}
