import React from "react";
import { useTranslation } from "react-i18next";
import { FormGroup, Input, FormText } from "reactstrap";

import "./Description.css";

export default function Description({
  syllabus,
  values,
  errors,
  handleChange,
}) {
  const { t } = useTranslation();

  return (
    <>
      <FormGroup className="row">
        <div className="col-lg-12">
          <Input style={{minHeight: "400px"}}
          type="textarea" 
          placeholder={t("admin.syllabus_edit.SUBJECT_DESCRIPTION_PLACEHOLDER")}
          name="description_subjectDescription"
          value={values.description_subjectDescription}
          onChange={handleChange}
          invalid={errors.description_subjectDescription} />
          {errors.description_subjectDescription && <FormText color="danger">{errors.description_subjectDescription}</FormText>}
        </div>
      </FormGroup>{" "}
      <br />
    </>
  );
}
