import React from "react";
import { useTranslation } from "react-i18next";
import { FaCog, FaEdit, FaPlus } from "react-icons/fa";
import { Button, Input, Table } from "reactstrap";
import * as lodash from "lodash";

import "./TableBody.css";

export default function TableBody({
  titles,
  groupItems,
  items,
  handleCheckItemOnChange,
  handleCheckAllOnChange,
  handleEditOnClick,
  handleAddOnClick,
}) {
  const { t } = useTranslation();

  return (
    <div className="table-body">
      <Table responsive bordered>
        <thead>
          <tr>
            <th className="align-middle">
              <Input
                type="checkbox"
                className="align-checkbox"
                id="output-standard-check-all"
                onChange={handleCheckAllOnChange}
              />
            </th>
            {titles &&
              titles.map((title, index) => (
                <th key={index}>
                  {title.icon}&nbsp;{title.text}
                </th>
              ))}
            <th data-value="options">
              <FaCog /> {t("admin.common.OPTIONS")}
            </th>
          </tr>
        </thead>

        <tbody>
          {groupItems &&
            groupItems.map((group) => {
              return (
                <>
                  <tr key={group.id}>
                    <td colSpan={4}>
                      <b>{group.ma}</b>
                    </td>
                    <td>
                      <Button
                        color="light"
                        title={t("admin.common.ADD")}
                        onClick={() => handleAddOnClick(group)}
                      >
                        <FaPlus fill="green" />
                      </Button>
                    </td>
                  </tr>
                  {items &&
                    items.map((item) => {
                      if (parseInt(item.mucTieuMonHoc) === parseInt(group.id)) {
                        return (
                          <>
                            <tr
                              key={`row-${item.id}`}
                              className="output-standard-row-table"
                              id={`output-standard-row-${item.id}`}
                            >
                              <td className="align-middle">
                                <Input
                                  type="checkbox"
                                  className="align-checkbox"
                                  name="output-standard-row-checkbox"
                                  id={`output-standard-checkbox-${item.id}`}
                                  value={item.id}
                                  onChange={() =>
                                    handleCheckItemOnChange(item.id)
                                  }
                                />
                              </td>
                              {titles &&
                                titles.map((title, index) => {
                                  if (title.field === "mucDo") {
                                    return (
                                      <td key={index}>
                                        {lodash.get(item, title.field) &&
                                          lodash
                                            .get(item, title.field)
                                            .map((level, index) => {
                                              return (
                                                <div key={index}>
                                                  {level.label}
                                                </div>
                                              );
                                            })}
                                      </td>
                                    );
                                  }
                                  return (
                                    <td key={index}>
                                      {lodash.get(item, title.field)}
                                    </td>
                                  );
                                })}
                              <td>
                                <Button
                                  color="light"
                                  title={t("admin.common.EDIT")}
                                  onClick={() => handleEditOnClick(item.id)}
                                >
                                  <FaEdit fill="blue" />
                                </Button>
                              </td>
                            </tr>
                          </>
                        );
                      }
                    })}
                </>
              );
            })}
        </tbody>
      </Table>
    </div>
  );
}
