import { useTranslation } from "react-i18next";
import React, { useState } from "react";
import Select from "react-select";
import {
  FaCode,
  FaColumns,
  FaExchangeAlt,
  FaFileAlt,
  FaSync,
  FaTrash,
} from "react-icons/fa";
import {
  Button,
  Form,
  FormGroup,
  FormText,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
  Modal,
  ModalHeader,
  ModalBody,
} from "reactstrap";
import { Formik } from "formik";
import * as Yup from "yup";

import "./UpdateModal.css";

import AsteriskIcon from "components/common/AsteriskIcon/AsteriskIcon";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import i18n from "i18n/i18n";

export default function UpdateModal({
  selectedItem,
  modal,
  toggle,
  headerTitle,
  handleUpdateOnClick,
  handleCancelOnClick,
  subjectGoals,
}) {
  const { t } = useTranslation();

  const [isLoading, setIsLoading] = useState(false);
  const levels = [
    {
      value: 1,
      label: "I",
    },
    {
      value: 2,
      label: "T",
    },
    {
      value: 3,
      label: "U",
    },
  ];

  return (
    <Modal isOpen={modal} toggle={toggle}>
      <ModalHeader toggle={toggle}>{headerTitle}</ModalHeader>
      <ModalBody>
        <div className="page">
          {isLoading && <LoadingBar />}

          <div className="page-content">
            <Formik
              initialValues={{
                id: selectedItem ? selectedItem.id : "",
                ma: selectedItem ? selectedItem.ma : "",
                moTa: selectedItem ? selectedItem.moTa : "",
                mucTieuMonHoc: selectedItem
                  ? selectedItem.mucTieuMonHoc
                  : subjectGoals[0]
                  ? subjectGoals[0].id
                  : "",
                mucDo: selectedItem ? selectedItem.mucDo : [],
              }}
              enableReinitialize={true}
              validationSchema={Yup.object().shape({
                ma: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
                moTa: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
              })}
            >
              {({
                values,
                errors,
                isValid,
                handleChange,
                handleReset,
                setFieldValue,
              }) => (
                <Form>
                  <FormGroup className="row">
                    <Label for="ma" className="col-lg-3">
                      {t("admin.subject_output_standard_edit.CODE")}{" "}
                      <AsteriskIcon />
                    </Label>
                    <div className="col-lg-9">
                      <InputGroup>
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <FaCode />
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input
                          type="text"
                          name="ma"
                          placeholder={t(
                            "admin.subject_output_standard_edit.CODE_PLACEHOLDER"
                          )}
                          value={values.ma}
                          onChange={handleChange}
                          invalid={errors.ma}
                        />
                      </InputGroup>
                      {errors.ma && (
                        <FormText color="danger">{errors.ma}</FormText>
                      )}
                    </div>
                  </FormGroup>{" "}
                  <br />
                  <FormGroup className="row">
                    <Label for="moTa" className="col-lg-3">
                      {t("admin.subject_output_standard_edit.DESCRIPTION")}{" "}
                      <AsteriskIcon />
                    </Label>
                    <div className="col-lg-9">
                      <InputGroup>
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <FaFileAlt />
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input
                          type="textarea"
                          name="moTa"
                          placeholder={t(
                            "admin.subject_output_standard_edit.DESCRIPTION_PLACEHOLDER"
                          )}
                          value={values.moTa}
                          onChange={handleChange}
                          invalid={errors.moTa}
                        />
                      </InputGroup>
                      {errors.moTa && (
                        <FormText color="danger">{errors.moTa}</FormText>
                      )}
                    </div>
                  </FormGroup>{" "}
                  <br />
                  <FormGroup className="row">
                    <Label for="mucTieuMonHoc" className="col-lg-3">
                      {t("admin.subject_output_standard_edit.SUBJECT_GOAL")}
                    </Label>
                    <div className="col-lg-9">
                      <InputGroup>
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <FaColumns />
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input
                          type="select"
                          name="mucTieuMonHoc"
                          value={values.mucTieuMonHoc}
                          onChange={handleChange}
                        >
                          {subjectGoals &&
                            subjectGoals.map((item, index) => {
                              return (
                                <option key={index} value={item.id}>
                                  {item.ma}
                                </option>
                              );
                            })}
                        </Input>
                      </InputGroup>
                    </div>
                  </FormGroup>{" "}
                  <br />
                  <FormGroup className="row">
                    <Label for="mucDo" className="col-lg-3">
                      {t("admin.subject_output_standard_edit.LEVEL")}
                    </Label>
                    <div className="col-lg-9">
                      <Select
                        value={values.mucDo}
                        onChange={(e) =>
                          setFieldValue("mucDo", Array.isArray(e) ? e : [])
                        }
                        isMulti
                        closeMenuOnSelect={false}
                        options={levels}
                        placeholder={t(
                          "admin.subject_output_standard_edit.LEVEL_PLACEHOLDER"
                        )}
                      />
                    </div>
                  </FormGroup>{" "}
                  <br />
                  <FormGroup>
                    <Button
                      color="success"
                      disabled={!isValid}
                      onClick={() => handleUpdateOnClick(values, isValid)}
                    >
                      <FaExchangeAlt /> {t("admin.common.UPDATE")}
                    </Button>
                    &emsp;
                    <Button onClick={handleReset}>
                      <FaSync /> {t("admin.common.REFRESH")}
                    </Button>
                    &emsp;
                    <Button color="danger" onClick={handleCancelOnClick}>
                      <FaTrash /> {t("admin.common.CANCEL")}
                    </Button>
                  </FormGroup>
                </Form>
              )}
            </Formik>
          </div>
        </div>
      </ModalBody>
    </Modal>
  );
}
