import { useTranslation } from "react-i18next";
import React, { useEffect, useState } from "react";
import { toast } from "react-toastify";
import Select from "react-select";
import {
  FaCode,
  FaExchangeAlt,
  FaFileAlt,
  FaSync,
  FaTrash,
} from "react-icons/fa";
import {
  Button,
  Form,
  FormGroup,
  FormText,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
  Modal,
  ModalHeader,
  ModalBody,
} from "reactstrap";
import { Formik } from "formik";
import * as Yup from "yup";

import "./UpdateModal.css";

import CDIOOutputStandardAPI from "api/common/education-major-output-standard";
import AsteriskIcon from "components/common/AsteriskIcon/AsteriskIcon";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import i18n from "i18n/i18n";

export default function UpdateModal({
  selectedItem,
  modal,
  toggle,
  headerTitle,
  handleUpdateOnClick,
  handleCancelOnClick,
}) {
  const { t } = useTranslation();

  const [isLoading, setIsLoading] = useState(false);
  const [CDIOOutputStandards, setCDIOOutputStandards] = useState([]);

  useEffect(async () => {
    try {
      const CDIOOutputStandardsRes = await CDIOOutputStandardAPI.getAll();
      const CDIOOutputStandardsOption = CDIOOutputStandardsRes.contents.map(
        (item) => {
          return {
            value: item.id,
            label: item.ma,
          };
        }
      );
      setCDIOOutputStandards(CDIOOutputStandardsOption);// eslint-disable-next-line
    } catch (err) {
    } finally {
      setIsLoading(false);
    }
  }, []);

  return (
    <Modal isOpen={modal} toggle={toggle}>
      <ModalHeader toggle={toggle}>{headerTitle}</ModalHeader>
      <ModalBody>
        <div className="page">
          {isLoading && <LoadingBar />}

          <div className="page-content">
            <Formik
              initialValues={{
                id: selectedItem ? selectedItem.id : "",
                ma: selectedItem ? selectedItem.ma : "",
                moTa: selectedItem ? selectedItem.moTa : "",
                chuanDauRaCDIO: selectedItem ? selectedItem.chuanDauRaCDIO : [],
              }}
              enableReinitialize={true}
              validationSchema={Yup.object().shape({
                ma: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
                moTa: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
              })}
            >
              {({
                values,
                errors,
                isValid,
                handleChange,
                handleReset,
                setFieldValue,
              }) => (
                <Form>
                  <FormGroup className="row">
                    <Label for="ma" className="col-lg-3">
                      {t("admin.subject_goal_edit.CODE")} <AsteriskIcon />
                    </Label>
                    <div className="col-lg-9">
                      <InputGroup>
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <FaCode />
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input
                          type="text"
                          name="ma"
                          placeholder={t(
                            "admin.subject_goal_edit.CODE_PLACEHOLDER"
                          )}
                          value={values.ma}
                          onChange={handleChange}
                          invalid={errors.ma}
                        />
                      </InputGroup>
                      {errors.ma && (
                        <FormText color="danger">{errors.ma}</FormText>
                      )}
                    </div>
                  </FormGroup>{" "}
                  <br />
                  <FormGroup className="row">
                    <Label for="moTa" className="col-lg-3">
                      {t("admin.subject_goal_edit.DESCRIPTION")}{" "}
                      <AsteriskIcon />
                    </Label>
                    <div className="col-lg-9">
                      <InputGroup>
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <FaFileAlt />
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input
                          type="textarea"
                          name="moTa"
                          placeholder={t(
                            "admin.subject_goal_edit.DESCRIPTION_PLACEHOLDER"
                          )}
                          value={values.moTa}
                          onChange={handleChange}
                          invalid={errors.moTa}
                        />
                      </InputGroup>
                      {errors.moTa && (
                        <FormText color="danger">{errors.moTa}</FormText>
                      )}
                    </div>
                  </FormGroup>{" "}
                  <br />
                  <FormGroup className="row">
                    <Label for="chuanDauRaCDIO" className="col-lg-3">
                      {t("admin.subject_goal_edit.CDIO_OUTPUT_STANDARD")}
                    </Label>
                    <div className="col-lg-9">
                      <Select
                        value={values.chuanDauRaCDIO}
                        onChange={(e) =>
                          setFieldValue(
                            "chuanDauRaCDIO",
                            Array.isArray(e) ? e : []
                          )
                        }
                        isMulti
                        closeMenuOnSelect={false}
                        options={CDIOOutputStandards}
                        placeholder={t(
                          "admin.subject_goal_edit.CDIO_OUTPUT_STANDARD_PLACEHOLDER"
                        )}
                      />
                    </div>
                  </FormGroup>{" "}
                  <br />
                  <FormGroup>
                    <Button
                      color="success"
                      disabled={!isValid}
                      onClick={() => handleUpdateOnClick(values, isValid)}
                    >
                      <FaExchangeAlt /> {t("admin.common.UPDATE")}
                    </Button>
                    &emsp;
                    <Button onClick={handleReset}>
                      <FaSync /> {t("admin.common.REFRESH")}
                    </Button>
                    &emsp;
                    <Button color="danger" onClick={handleCancelOnClick}>
                      <FaTrash /> {t("admin.common.CANCEL")}
                    </Button>
                  </FormGroup>
                </Form>
              )}
            </Formik>
          </div>
        </div>
      </ModalBody>
    </Modal>
  );
}
