import React from "react";
import { useTranslation } from "react-i18next";
import { FormGroup, Input, FormText } from "reactstrap";
import "./Rule.css";

export default function Rule({ syllabus, values, errors, setFieldValue }) {
  const { t } = useTranslation();

  return (
    <>
      <FormGroup className="row">
        <div className="col-lg-12">
          <Input style={{minHeight: "400px"}}
            type="textarea"
            placeholder={t("admin.syllabus_edit.RULE_PLACEHOLDER")}
            name="rule_rule"
            value={values.rule_rule}
            onChange={e => setFieldValue("rule_rule", e.target.value)}
            invalid={errors.rule_rule}
          />
          {errors.rule_rule && (
            <FormText color="danger">{errors.rule_rule}</FormText>
          )}
        </div>
      </FormGroup>{" "}
      <br />
    </>
  );
}
