import { useTranslation } from "react-i18next";
import React from "react";
import {
  FaCode,
  FaExchangeAlt,
  FaSync,
  FaTextWidth,
  FaTrash,
} from "react-icons/fa";
import {
  Button,
  Form,
  FormGroup,
  FormText,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
  Modal,
  ModalHeader,
  ModalBody,
} from "reactstrap";
import { Formik } from "formik";
import * as Yup from "yup";

import "./UpdateModal.css";

import AsteriskIcon from "components/common/AsteriskIcon/AsteriskIcon";
import i18n from "i18n/i18n";

export default function UpdateModal({
  selectedItem,
  modal,
  toggle,
  headerTitle,
  handleUpdateOnClick,
  handleCancelOnClick,
}) {
  const { t } = useTranslation();

  return (
    <Modal isOpen={modal} toggle={toggle}>
      <ModalHeader toggle={toggle}>{headerTitle}</ModalHeader>
      <ModalBody>
        <div className="page">
          <div className="page-content">
            <Formik
              initialValues={{
                id: selectedItem ? selectedItem.id : "",
                ma: selectedItem ? selectedItem.ma : "",
                ten: selectedItem ? selectedItem.ten : "",
              }}
              enableReinitialize={true}
              validationSchema={Yup.object().shape({
                ma: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
                ten: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
              })}
            >
              {({
                values,
                errors,
                isValid,
                handleChange,
                handleReset,
                setFieldValue,
              }) => (
                <Form>
                  <FormGroup className="row">
                    <Label for="ma" className="col-lg-3">
                      {t("admin.teaching_activity_list.CODE")} <AsteriskIcon />
                    </Label>
                    <div className="col-lg-9">
                      <InputGroup>
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <FaCode />
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input
                          type="text"
                          name="ma"
                          placeholder={t(
                            "admin.teaching_activity_list.CODE_PLACEHOLDER"
                          )}
                          value={values.ma}
                          onChange={handleChange}
                          invalid={errors.ma}
                        />
                      </InputGroup>
                      {errors.ma && (
                        <FormText color="danger">{errors.ma}</FormText>
                      )}
                    </div>
                  </FormGroup>{" "}
                  <br />
                  <FormGroup className="row">
                    <Label for="ten" className="col-lg-3">
                      {t("admin.teaching_activity_list.NAME")} <AsteriskIcon />
                    </Label>
                    <div className="col-lg-9">
                      <InputGroup>
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <FaTextWidth />
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input
                          type="text"
                          name="ten"
                          placeholder={t(
                            "admin.teaching_activity_list.NAME_PLACEHOLDER"
                          )}
                          value={values.ten}
                          onChange={handleChange}
                          invalid={errors.ten}
                        />
                      </InputGroup>
                      {errors.ten && (
                        <FormText color="danger">{errors.ten}</FormText>
                      )}
                    </div>
                  </FormGroup>{" "}
                  <br />
                  <FormGroup>
                    <Button
                      color="success"
                      disabled={!isValid}
                      onClick={() => handleUpdateOnClick(values, isValid)}
                    >
                      <FaExchangeAlt /> {t("admin.common.UPDATE")}
                    </Button>
                    &emsp;
                    <Button onClick={handleReset}>
                      <FaSync /> {t("admin.common.REFRESH")}
                    </Button>
                    &emsp;
                    <Button color="danger" onClick={handleCancelOnClick}>
                      <FaTrash /> {t("admin.common.CANCEL")}
                    </Button>
                  </FormGroup>
                </Form>
              )}
            </Formik>
          </div>
        </div>
      </ModalBody>
    </Modal>
  );
}
