import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useHistory, useLocation } from "react-router-dom";
import { toast } from "react-toastify";
import { FaExchangeAlt, FaSave, FaSync, FaTrash } from "react-icons/fa";
import { Button, Form, FormGroup } from "reactstrap";
import queryString from "query-string";
import { Formik } from "formik";
import * as Yup from "yup";

import "./SyllabusEdit.css";

import { RoutesString } from "pages/routesString";
import syllabusAPI from "api/common/syllabus";
import subjectPrerequisiteAPI from "api/common/subject-prerequisite";
import subjectGoalAPI from "api/common/subject-goal";
import subjectOutputStandardAPI from "api/common/subject-output-standard";
import evaluationTypeAPI from "api/common/evaluation-type";
import evaluationActivityAPI from "api/common/evaluation-activity";
import teachingActivityAPI from "api/common/teaching-activity";
import topicAPI from "api/common/topic";
import generalSyllbusAPI from "api/common/general-syllabus";
import Breadcrumbs from "components/admin/Breadcrumbs/Breadcrumbs";
import Notification from "components/common/Notification/Notification";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import PageHeader from "./components/PageHeader/PageHeader";
import Section from "./components/Section/Section";
import Basic from "./components/syllabus-content/Basic/Basic";
import Description from "./components/syllabus-content/Description/Description";
import Goal from "./components/syllabus-content/Goal/Goal";
import OutputStandard from "./components/syllabus-content/OutputStandard/OutputStandard";
import EvaluationType from "./components/syllabus-content/EvaluationType/EvaluationType";
import Evaluation from "./components/syllabus-content/Evaluation/Evaluation";
import TeachingActivity from "./components/syllabus-content/TeachingActivity/TeachingActivity";
import TheoreticalTeachingPlan from "./components/syllabus-content/TheoreticalTeachingPlan/TheoreticalTeachingPlan";
import PracticeTeachingPlan from "./components/syllabus-content/PracticeTeachingPlan/PracticeTeachingPlan";
import Resource from "./components/syllabus-content/Resource/Resource";
import Rule from "./components/syllabus-content/Rule/Rule";
import i18n from "i18n/i18n";

export default function SyllabusEdit() {
  const { t } = useTranslation();

  const history = useHistory();

  const location = useLocation();

  const [id, setId] = useState(null);
  const [initialValues, setInitialValues] = useState({
    basic_subjectVietnameseName: "",
    basic_subjectEnglishName: "",
    basic_subjectCode: "",
    basic_numberOfCredits: "",
    basic_numberOfTheoreticalPeriods: "",
    basic_numberOfPracticePeriods: "",
    basic_numberOfSelfStudyPeriods: "",
    basic_subjectPrerequisites: [],
    basic_selectedSubjectPrerequisites: [],
    description_subjectDescription: "",
    goal_subjectGoals: [],
    outputStandard_subjectOutputStandards: [],
    evaluation_evaluationTypes: [],
    evaluation_evaluationActivities: [],
    teachingActivity_teachingActivities: [],
    theoreticalTeachingPlan_topics: [],
    practiceTeachingPlan_topics: [],
    resource_resource: "",
    rule_rule: "",
  });
  const [notification, setNotification] = useState({
    color: "danger",
    isOpen: false,
    messages: [],
  });
  const [isLoading, setIsLoading] = useState(false);
  const [syllabus, setSyllabus] = useState(null);

  useEffect(async () => {
    const parsed = queryString.parse(window.location.search);
    if (parsed.id) {
      setId(parsed.id);
      updateForm(parsed.id);
    }
  }, [location]);

  const updateForm = async function (id) {
    setIsLoading(true);
    try {
      const syllabusRes = await syllabusAPI.getById(id);
      setSyllabus(syllabusRes);
      const subjectPrerequisitesRes = await subjectPrerequisiteAPI.getBySubjectId(
        syllabusRes.monHoc.id
      );
      const subjectPrerequisitesOption = subjectPrerequisitesRes.contents.map(
        (item) => ({
          value: item.monHocTruoc.id,
          label: item.monHocTruoc.ma,
        })
      );
      const localSyllabusValues = JSON.parse(
        localStorage.getItem("localSyllabusValues")
      );// eslint-disable-next-line
      console.log(localSyllabusValues);
      setInitialValues({
        basic_subjectVietnameseName: syllabusRes.monHoc.tenTiengViet,
        basic_subjectEnglishName: syllabusRes.monHoc.tenTiengAnh,
        basic_subjectCode: syllabusRes.monHoc.ma,
        basic_numberOfCredits: syllabusRes.monHoc.soTinChi,
        basic_numberOfTheoreticalPeriods: syllabusRes.monHoc.soTietLyThuyet,
        basic_numberOfPracticePeriods: syllabusRes.monHoc.soTietThucHanh,
        basic_numberOfSelfStudyPeriods: syllabusRes.monHoc.soTietTuHoc,
        basic_subjectPrerequisites: subjectPrerequisitesOption,
        basic_selectedSubjectPrerequisites: subjectPrerequisitesOption,
        description_subjectDescription: localSyllabusValues
          ? localSyllabusValues.description_subjectDescription
          : syllabusRes.moTa,
        goal_subjectGoals: localSyllabusValues
          ? localSyllabusValues.goal_subjectGoals
          : [],
        outputStandard_subjectOutputStandards: localSyllabusValues
          ? localSyllabusValues.outputStandard_subjectOutputStandards
          : [],
        evaluation_evaluationTypes: localSyllabusValues
          ? localSyllabusValues.evaluation_evaluationTypes
          : [],
        evaluation_evaluationActivities: localSyllabusValues
          ? localSyllabusValues.evaluation_evaluationActivities
          : [],
        teachingActivity_teachingActivities: localSyllabusValues
          ? localSyllabusValues.teachingActivity_teachingActivities
          : [],
        theoreticalTeachingPlan_topics: localSyllabusValues
          ? localSyllabusValues.theoreticalTeachingPlan_topics
          : [],
        practiceTeachingPlan_topics: localSyllabusValues
          ? localSyllabusValues.practiceTeachingPlan_topics
          : [],
        resource_resource: localSyllabusValues
          ? localSyllabusValues.resource_resource
          : syllabusRes.taiNguyen,
        rule_rule: localSyllabusValues
          ? localSyllabusValues.rule_rule
          : syllabusRes.quiDinh,
      });
    } catch (err) {
      if (err.response) {
        if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
          toast.error(t("RESPONSE_MESSAGE403"));
        }
        else if (err?.response?.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("RESPONSE_MESSAGE404"));
        } else {
          toast.error(t("RESPONSE_MESSAGE500"));
        }
      }
    } finally {
      setIsLoading(false);
    }
  };

  const handleEditOnClick = async function (values, isValid) {
    if (!isValid) {
      return;
    }
    setIsLoading(true);
    try {
      const body = {
        description_subjectDescription: values.description_subjectDescription,
        resource_resource: values.resource_resource,
        rule_rule: values.rule_rule,
        goal_subjectGoals: values.goal_subjectGoals.map(item => {
          return {
            ...item,
            syllabus: id,
            chuanDauRaCDIO: item.chuanDauRaCDIO.map(item => item.value)
          }
        }),
        outputStandard_subjectOutputStandards: values.outputStandard_subjectOutputStandards.map(item => {
          return {
            ...item,
            mucDo: item.mucDo.map(item => item.label).join(", ")
          }
        }),
        evaluation_evaluationTypes: values.evaluation_evaluationTypes.map(item => {
          return {
            ...item,
            chuanDauRaMonHoc: item.chuanDauRaMonHoc.map(item => item.value)
          }
        }),
        evaluation_evaluationActivities: values.evaluation_evaluationActivities.map(item => {
          return {
            ...item,
            chuanDauRaMonHoc: item.chuanDauRaMonHoc.map(item => item.value)
          }
        }),
        teachingActivity_teachingActivities: values.teachingActivity_teachingActivities,
        theoreticalTeachingPlan_topics: values.theoreticalTeachingPlan_topics.map(item => {
          return {
            ...item,
            chuanDauRaMonHoc: item.chuanDauRaMonHoc.map(item => item.value),
            hoatDongDanhGia: item.hoatDongDanhGia.map(item => item.value),
            hoatDongDayHoc: item.hoatDongDayHoc.map(item => item.value)
          }
        }),
        practiceTeachingPlan_topics: values.practiceTeachingPlan_topics.map(item => {
          return {
            ...item,
            chuanDauRaMonHoc: item.chuanDauRaMonHoc.map(item => item.value),
            hoatDongDanhGia: item.hoatDongDanhGia.map(item => item.value),
            hoatDongDayHoc: item.hoatDongDayHoc.map(item => item.value)
          }
        })
      }// eslint-disable-next-line
      console.log(JSON.stringify(body));
      const res = await generalSyllbusAPI.putById(id, body);
      toast.success("ADD_SUCCESS");
    } catch (err) {
      if (err?.response) {
        if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
          toast.error(t("RESPONSE_MESSAGE403"));
        }
        else if (err?.response?.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("RESPONSE_MESSAGE404"));
        } else {
          if (String(err?.response?.data?.error)?.includes("ER_DUP_ENTRY")) {
            toast.error(t("ER_DUP_ENTRY"));
          } else if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
            toast.error(t("RESPONSE_MESSAGE404"));
          } else {
            toast.error(t("RESPONSE_MESSAGE500"));
          }
        }
      }
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="page">
      {isLoading && <LoadingBar />}

      <Breadcrumbs
        currentItem={t("admin.syllabus_edit.ADD_SYLLABUS")}
        items={[
          {
            href: RoutesString.ADMIN_SYLLABUS_GENERATE,
            title: t("admin.syllabus_edit.SYLLABUS_GENERATE"),
          },
        ]}
      />

      <PageHeader titleHeader={t("admin.syllabus_edit.ADD_SYLLABUS")} />

      <div className="page-content">
        <Formik
          initialValues={initialValues}
          enableReinitialize={true}
          validationSchema={Yup.object().shape({
            basic_numberOfCredits: Yup.number().required(
              i18n.t("REQUIRED_FIELD_WARNING")
            ),
            basic_numberOfTheoreticalPeriods: Yup.number().required(
              i18n.t("REQUIRED_FIELD_WARNING")
            ),
            basic_numberOfPracticePeriods: Yup.number().required(
              i18n.t("REQUIRED_FIELD_WARNING")
            ),
            basic_numberOfSelfStudyPeriods: Yup.number().required(
              i18n.t("REQUIRED_FIELD_WARNING")
            ),
          })}
        >
          {({
            values,
            errors,
            isValid,
            handleChange,
            handleReset,
            setFieldValue,
          }) => (
            <Form>
              <Notification
                color={notification.color}
                isOpen={notification.isOpen}
                messages={notification.messages}
              />

              <Section
                title={t("admin.syllabus_edit.BASIC_TITLE")}
                content={<Basic values={values} />}
              />

              <Section
                title={t("admin.syllabus_edit.DESCRIPTION_TITLE")}
                content={
                  <Description
                    syllabus={syllabus}
                    values={values}
                    errors={errors}
                    handleChange={handleChange}
                  />
                }
              />

              <Section
                title={t("admin.syllabus_edit.GOAL_TITLE")}
                content={
                  <Goal
                    syllabus={syllabus}
                    values={values}
                    errors={errors}
                    setFieldValue={setFieldValue}
                  />
                }
              />

              <Section
                title={t("admin.syllabus_edit.OUTPUT_STANDARD_TITLE")}
                content={
                  <OutputStandard
                    syllabus={syllabus}
                    values={values}
                    errors={errors}
                    setFieldValue={setFieldValue}
                  />
                }
              />

              <Section
                title={t("admin.syllabus_edit.EVALUATION_TYPE_TITLE")}
                content={
                  <EvaluationType
                    syllabus={syllabus}
                    values={values}
                    errors={errors}
                    setFieldValue={setFieldValue}
                  />
                }
              />

              <Section
                title={t("admin.syllabus_edit.EVALUATION_TITLE")}
                content={
                  <Evaluation
                    syllabus={syllabus}
                    values={values}
                    errors={errors}
                    setFieldValue={setFieldValue}
                  />
                }
              />

              <Section
                title={t("admin.syllabus_edit.TEACHING_ACTIVITY_TITLE")}
                content={
                  <TeachingActivity
                    syllabus={syllabus}
                    values={values}
                    errors={errors}
                    setFieldValue={setFieldValue}
                  />
                }
              />

              <Section
                title={t("admin.syllabus_edit.THEORETICAL_TEACHING_PLAN_TITLE")}
                content={
                  <TheoreticalTeachingPlan
                    syllabus={syllabus}
                    values={values}
                    errors={errors}
                    setFieldValue={setFieldValue}
                  />
                }
              />

              <Section
                title={t("admin.syllabus_edit.PRACTICE_TEACHING_PLAN_TITLE")}
                content={
                  <PracticeTeachingPlan
                    syllabus={syllabus}
                    values={values}
                    errors={errors}
                    setFieldValue={setFieldValue}
                  />
                }
              />

              <Section
                title={t("admin.syllabus_edit.RESOURCE_TITLE")}
                content={
                  <Resource
                    syllabus={syllabus}
                    values={values}
                    errors={errors}
                    setFieldValue={setFieldValue}
                  />
                }
              />

              <Section
                title={t("admin.syllabus_edit.RULE_TITLE")}
                content={
                  <Rule
                    syllabus={syllabus}
                    values={values}
                    errors={errors}
                    setFieldValue={setFieldValue}
                  />
                }
              />

              <FormGroup>
                <Button
                  color="success"
                  disabled={!isValid}
                  onClick={() => handleEditOnClick(values, isValid)}
                >
                  <FaExchangeAlt /> {t("admin.common.UPDATE")}
                </Button>
                &emsp;
                <Button onClick={handleReset}>
                  <FaSync /> {t("admin.common.REFRESH")}
                </Button>
                &emsp;
                <Button
                  color="primary"
                  onClick={() => {
                    localStorage.setItem(
                      "localSyllabusValues",
                      JSON.stringify(values)
                    );
                    toast.success("SAVED");
                  }}
                >
                  <FaSave /> {t("admin.common.SAVE")}
                </Button>
              </FormGroup>
            </Form>
          )}
        </Formik>
      </div>
    </div>
  );
}
