import { useTranslation } from "react-i18next";
import React from "react";
import Select from "react-select";
import {
  FaCalendar,
  FaCode,
  FaColumns,
  FaPlus,
  FaTextWidth,
  FaTrash,
} from "react-icons/fa";
import {
  Button,
  Form,
  FormGroup,
  FormText,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
  Modal,
  ModalHeader,
  ModalBody,
} from "reactstrap";
import { Formik } from "formik";
import * as Yup from "yup";

import "./AddModal.css";

import AsteriskIcon from "components/common/AsteriskIcon/AsteriskIcon";
import i18n from "i18n/i18n";

export default function AddModal({
  modal,
  toggle,
  headerTitle,
  handleAddOnClick,
  handleCancelOnClick,
  teachingPlanTypes,
  subjectOutputStandards,
  teachingActivities,
  evaluationActivities,
}) {
  const { t } = useTranslation();

  const subjectOutputStandardsOption = subjectOutputStandards.map((item) => {
    return { value: item.id, label: item.ma };
  });
  const teachingActivitiesOption = teachingActivities.map((item) => {
    return { value: item.id, label: item.ten };
  });
  const evaluationActivitiesOption = evaluationActivities.map((item) => {
    return { value: item.id, label: item.ma };
  });

  return (
    <Modal isOpen={modal} toggle={toggle}>
      <ModalHeader toggle={toggle}>{headerTitle}</ModalHeader>
      <ModalBody>
        <div className="page">
          <div className="page-content">
            <Formik
              initialValues={{
                ma: "",
                ten: "",
                tuan: "",
                idLKHGD: teachingPlanTypes[0] ? teachingPlanTypes[0].id : "",
                chuanDauRaMonHoc: [],
                hoatDongDayHoc: [],
                hoatDongDanhGia: [],
              }}
              enableReinitialize={true}
              validationSchema={Yup.object().shape({
                ma: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
                ten: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
                tuan: Yup.number().required(i18n.t("REQUIRED_FIELD_WARNING")),
              })}
            >
              {({
                values,
                errors,
                dirty,
                isValid,
                handleChange,
                setFieldValue,
              }) => (
                <Form>
                  <FormGroup className="row">
                    <Label for="ma" className="col-lg-3">
                      {t("admin.topic_edit.CODE")} <AsteriskIcon />
                    </Label>
                    <div className="col-lg-9">
                      <InputGroup>
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <FaCode />
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input
                          type="text"
                          name="ma"
                          placeholder={t("admin.topic_edit.CODE_PLACEHOLDER")}
                          value={values.ma}
                          onChange={handleChange}
                          invalid={errors.ma}
                        />
                      </InputGroup>
                      {errors.ma && (
                        <FormText color="danger">{errors.ma}</FormText>
                      )}
                    </div>
                  </FormGroup>{" "}
                  <br />
                  <FormGroup className="row">
                    <Label for="ten" className="col-lg-3">
                      {t("admin.topic_edit.NAME")} <AsteriskIcon />
                    </Label>
                    <div className="col-lg-9">
                      <InputGroup>
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <FaTextWidth />
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input
                          type="text"
                          name="ten"
                          placeholder={t("admin.topic_edit.NAME_PLACEHOLDER")}
                          value={values.ten}
                          onChange={handleChange}
                          invalid={errors.ten}
                        />
                      </InputGroup>
                      {errors.ten && (
                        <FormText color="danger">{errors.ten}</FormText>
                      )}
                    </div>
                  </FormGroup>{" "}
                  <br />
                  <FormGroup className="row">
                    <Label for="tuan" className="col-lg-3">
                      {t("admin.topic_edit.WEEK")} <AsteriskIcon />
                    </Label>
                    <div className="col-lg-9">
                      <InputGroup>
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <FaCalendar />
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input
                          type="number"
                          name="tuan"
                          placeholder={t("admin.topic_edit.WEEK_PLACEHOLDER")}
                          value={values.tuan}
                          onChange={handleChange}
                          invalid={errors.tuan}
                        />
                      </InputGroup>
                      {errors.tuan && (
                        <FormText color="danger">{errors.tuan}</FormText>
                      )}
                    </div>
                  </FormGroup>{" "}
                  <br />
                  <FormGroup className="row">
                    <Label for="chuanDauRaMonHoc" className="col-lg-3">
                      {t("admin.topic_edit.SUBJECT_OUTPUT_STANDARD")}
                    </Label>
                    <div className="col-lg-9">
                      <Select
                        value={values.chuanDauRaMonHoc}
                        onChange={(e) =>
                          setFieldValue(
                            "chuanDauRaMonHoc",
                            Array.isArray(e) ? e : []
                          )
                        }
                        isMulti
                        closeMenuOnSelect={false}
                        options={subjectOutputStandardsOption}
                        placeholder={t(
                          "admin.topic_edit.SUBJECT_OUTPUT_STANDARD_PLACEHOLDER"
                        )}
                      />
                    </div>
                  </FormGroup>{" "}
                  <br />
                  <FormGroup className="row">
                    <Label for="hoatDongDayHoc" className="col-lg-3">
                      {t("admin.topic_edit.TEACHING_ACTIVITY")}
                    </Label>
                    <div className="col-lg-9">
                      <Select
                        value={values.hoatDongDayHoc}
                        onChange={(e) =>
                          setFieldValue(
                            "hoatDongDayHoc",
                            Array.isArray(e) ? e : []
                          )
                        }
                        isMulti
                        closeMenuOnSelect={false}
                        options={teachingActivitiesOption}
                        placeholder={t(
                          "admin.topic_edit.TEACHING_ACTIVITY_PLACEHOLDER"
                        )}
                      />
                    </div>
                  </FormGroup>{" "}
                  <br />
                  <FormGroup className="row">
                    <Label for="hoatDongDanhGia" className="col-lg-3">
                      {t("admin.topic_edit.EVALUATION_ACTIVITY")}
                    </Label>
                    <div className="col-lg-9">
                      <Select
                        value={values.hoatDongDanhGia}
                        onChange={(e) =>
                          setFieldValue(
                            "hoatDongDanhGia",
                            Array.isArray(e) ? e : []
                          )
                        }
                        isMulti
                        closeMenuOnSelect={false}
                        options={evaluationActivitiesOption}
                        placeholder={t(
                          "admin.topic_edit.EVALUATION_ACTIVITY_PLACEHOLDER"
                        )}
                      />
                    </div>
                  </FormGroup>{" "}
                  <br />
                  <FormGroup className="row">
                    <Label for="idLKHGD" className="col-lg-3">
                      {t("admin.topic_edit.TEACHING_PLAN_TYPE")}
                    </Label>
                    <div className="col-lg-9">
                      <InputGroup>
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <FaColumns />
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input
                          type="select"
                          name="idLKHGD"
                          value={values.idLKHGD}
                          onChange={handleChange}
                        >
                          {teachingPlanTypes &&
                            teachingPlanTypes.map((item, index) => {
                              return (
                                <option key={index} value={item.id}>
                                  {item.ten}
                                </option>
                              );
                            })}
                        </Input>
                      </InputGroup>
                    </div>
                  </FormGroup>{" "}
                  <br />
                  <FormGroup>
                    <Button
                      color="success"
                      disabled={!(isValid && dirty)}
                      onClick={() => handleAddOnClick(values, isValid && dirty)}
                    >
                      <FaPlus /> {t("admin.common.ADD")}
                    </Button>
                    &emsp;
                    <Button color="danger" onClick={handleCancelOnClick}>
                      <FaTrash /> {t("admin.common.CANCEL")}
                    </Button>
                  </FormGroup>
                </Form>
              )}
            </Formik>
          </div>
        </div>
      </ModalBody>
    </Modal>
  );
}
