import React from "react";
import { useTranslation } from "react-i18next";
import { FormGroup, Input, FormText } from "reactstrap";

import "./Resource.css";

export default function Resource({ syllabus, values, errors, setFieldValue }) {
  const { t } = useTranslation();

  return (
    <>
      <FormGroup className="row">
        <div className="col-lg-12">
          <Input style={{minHeight: "400px"}}
          type="textarea" 
          placeholder={t("admin.syllabus_edit.RESOURCE_PLACEHOLDER")}
          name="resource_resource"
          value={values.resource_resource}
          onChange={e => setFieldValue("resource_resource", e.target.value)}
          invalid={errors.resource_resource} />
          {errors.resource_resource && <FormText color="danger">{errors.resource_resource}</FormText>}
        </div>
      </FormGroup>{" "}
      <br />
    </>
  );
}
