import React from "react";
import { Pagination, PaginationItem, PaginationLink } from "reactstrap";

import "./MyPagination.css";

export default function MyPagination({
  startPage,
  page,
  limit,
  total,
  firstOnClick,
  previousOnClick,
  oneOnClick,
  twoOnClick,
  threeOnClick,
  nextOnClick,
  lastOnClick,
}) {
  return (
    <Pagination>
      <PaginationItem disabled={page === 1}>
        <PaginationLink first onClick={firstOnClick} />
      </PaginationItem>
      <PaginationItem disabled={page === 1}>
        <PaginationLink previous onClick={previousOnClick} />
      </PaginationItem>
      <PaginationItem active={page === startPage} disabled={total <= 0}>
        <PaginationLink onClick={oneOnClick}>{startPage}</PaginationLink>
      </PaginationItem>
      <PaginationItem
        active={page === startPage + 1}
        disabled={(startPage + 1) * limit - total >= limit}
      >
        <PaginationLink onClick={twoOnClick}>{startPage + 1}</PaginationLink>
      </PaginationItem>
      <PaginationItem
        active={page === startPage + 2}
        disabled={(startPage + 2) * limit - total >= limit}
      >
        <PaginationLink onClick={threeOnClick}>{startPage + 2}</PaginationLink>
      </PaginationItem>
      <PaginationItem disabled={(page + 1) * limit - total >= limit}>
        <PaginationLink next onClick={nextOnClick} />
      </PaginationItem>
      <PaginationItem disabled={(page + 1) * limit - total >= limit}>
        <PaginationLink last onClick={lastOnClick} />
      </PaginationItem>
    </Pagination>
  );
}
