import React from "react";
import { useTranslation } from "react-i18next";
import { FaCog, FaEdit, FaPlus } from "react-icons/fa";
import { Button, Input, Table } from "reactstrap";
import * as lodash from "lodash";

import "./TableBody.css";

export default function TableBody({
  titles,
  items,
  handleCheckItemOnChange,
  handleCheckAllOnChange,
  handleEditOnClick,
}) {
  const { t } = useTranslation();

  return (
    <div className="table-body">
      <Table responsive bordered>
        <thead>
          <tr>
            <th className="align-middle">
              <Input
                type="checkbox"
                className="align-checkbox"
                id="evaluation-type-check-all"
                onChange={handleCheckAllOnChange}
              />
            </th>
            {titles &&
              titles.map((title, index) => (
                <th key={index}>
                  {title.icon}&nbsp;{title.text}
                </th>
              ))}
            <th data-value="options">
              <FaCog /> {t("admin.common.OPTIONS")}
            </th>
          </tr>
        </thead>

        <tbody>
          {items &&
            items.map((item) => {
              return (
                <>
                  <tr
                    key={`row-${item.id}`}
                    className="evaluation-type-row-table"
                    id={`evaluation-type-row-${item.id}`}
                  >
                    <td className="align-middle">
                      <Input
                        type="checkbox"
                        className="align-checkbox"
                        name="evaluation-type-row-checkbox"
                        id={`evaluation-type-checkbox-${item.id}`}
                        value={item.id}
                        onChange={() => handleCheckItemOnChange(item.id)}
                      />
                    </td>
                    {titles &&
                      titles.map((title, index) => {
                        if (title.field === "chuanDauRaMonHoc") {
                          return (
                            <td key={index}>
                              {lodash.get(item, title.field) &&
                                lodash
                                  .get(item, title.field)
                                  .map((subjectOutputStandard, index) => {
                                    return (
                                      <div key={index}>
                                        {subjectOutputStandard.label}
                                      </div>
                                    );
                                  })}
                            </td>
                          );
                        }
                        return (
                          <td key={index}>{lodash.get(item, title.field)}</td>
                        );
                      })}
                    <td>
                      <Button
                        color="light"
                        title={t("admin.common.EDIT")}
                        onClick={() => handleEditOnClick(item.id)}
                      >
                        <FaEdit fill="blue" />
                      </Button>
                    </td>
                  </tr>
                </>
              );
            })}
        </tbody>
      </Table>
    </div>
  );
}
